import React from 'react'
import { Card } from 'react-bootstrap'

function ProjectCard(props) {
    return (
        <div>
            <Card style={{ width: '30rem' }}><a href={props.link}>
                <Card.Img variant="top" src={props.img} /></a>
                <Card.Body>
                    <Card.Title>{props.title}</Card.Title>
                    <Card.Text>
                        {props.massage}
                    </Card.Text>
                    {props.children}
                </Card.Body>
            </Card>

        </div>
    )
}

export default ProjectCard