import React from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'

function AboutMeBroad() {
    return (
        <section id="about" className="color-section">
            <Container fluid>
                <Row>
                    <Col lg="7">
                        <h1>Vachara Vongvittayamethkul</h1>
                        <h3>Web Developer</h3>
                        <Button onClick={()=>{window.location.href = process.env.PUBLIC_URL + "#contact"}}>Contact Me</Button>
                    </Col>
                    <Col lg="5">
                        <img src={process.env.PUBLIC_URL + '/my_profile.png'} alt="profile" />
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default AboutMeBroad